<script>
export default {
  name: "RegisterView",
  data() {
    return {
      student: {},
      username: "",
      password: "",
      Repassword: "",
    };
  },
  methods: {
    onSubmit(values) {
      console.log("submit", values);
    },
    RegisterClick() {
      if (this.username == "" || this.password == "" || this.Repassword == "") {
        alert("nonono~");
        return;
      }
      if (this.password === this.Repassword) {
        alert("注册成功");
        this.$router.push({ path: "/login" });
      } else {
        alert("两次密码不同");
      }
      this.student = {
        username: this.username,
        password: this.password,
        school: ["广东技术师范大学", "广州大学", "广东石油化工"],
        score: 100,
      };

      localStorage.setItem(this.username, JSON.stringify(this.student));
    },
  },
};
</script>

